
import React, { useEffect } from 'react';
import MainRouter from './router';
import i18n from './i18n/i18n';

const App: React.FC = () => {
	useEffect(() => {
		const query = new URLSearchParams(window.location.search);
		const paramLang = query.get('lang');
		const defaultLang = 'en';
		const lang = localStorage.getItem('i18nextLng');
		if (paramLang) {
			i18n.switchLanguage(paramLang);
		} else if (lang) {
			i18n.switchLanguage(lang || defaultLang);
		} else {
			i18n.switchLanguage(defaultLang);
		}
	}, []);

	return (
		<MainRouter />
	);
};

export default App;
